html {
  font-size: 62.5%;
}

.alignright {
  float: right;
  margin: 20px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 20px 20px 20px 0;
}

.wrapper {
  max-width: 1150px;
  width: 95%;
  margin: 0 auto;
}

.aligncenter {
  display: block;
  margin: 0 auto;
}

.alignwide {
  margin-left: -80px;
  margin-right: -80px;
}
.alignfull {
  margin-left: calc(-100vw / 2 + 100% / 2);
  margin-right: calc(-100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: 100vw;
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.alignfull img {
  width: 100vw;
}

// Visually Hidden
.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

table {
  width: 100%;
  margin-bottom: 15px;
}

td {
  border: 1px solid black;
  vertical-align: top;
}

sub a,
sup a {
  font-size: inherit;
}

hr {
  margin: 30px 0;
}

body {
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
}

button {
  background: none;
  border: none;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
ul,
p {
  margin: 0px;
  padding: 0px;
}

p,
li,
a,
ul,
ol,
table {
  font-size: 2rem;
}

img {
  max-width: 100%;
}

@media (max-width: $md) {
  .alignright,
  .alignleft {
    float: none !important;
    display: block;
    margin: 0 auto;
  }
}
