/*
=============================================================================
                   Normalize, border box and clearfix
=============================================================================
*/
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.clearfix:after {
  content: " ";
  visibility: hidden;
  display: block;
  height: 0;
  clear: both; }

html {
  font-size: 62.5%; }

.alignright {
  float: right;
  margin: 20px 0 20px 20px; }

.alignleft {
  float: left;
  margin: 20px 20px 20px 0; }

.wrapper {
  max-width: 1150px;
  width: 95%;
  margin: 0 auto; }

.aligncenter {
  display: block;
  margin: 0 auto; }

.alignwide {
  margin-left: -80px;
  margin-right: -80px; }

.alignfull {
  margin-left: calc(-100vw / 2 + 100% / 2);
  margin-right: calc(-100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: 100vw;
  padding-left: 0 !important;
  padding-right: 0 !important; }

.alignfull img {
  width: 100vw; }

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

table {
  width: 100%;
  margin-bottom: 15px; }

td {
  border: 1px solid black;
  vertical-align: top; }

sub a,
sup a {
  font-size: inherit; }

hr {
  margin: 30px 0; }

body {
  font-family: "Open Sans", sans-serif;
  line-height: 1.5; }

button {
  background: none;
  border: none; }

.flex {
  display: flex;
  flex-wrap: wrap; }

a {
  color: inherit;
  text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
ul,
p {
  margin: 0px;
  padding: 0px; }

p,
li,
a,
ul,
ol,
table {
  font-size: 2rem; }

img {
  max-width: 100%; }

@media (max-width: 768px) {
  .alignright,
  .alignleft {
    float: none !important;
    display: block;
    margin: 0 auto; } }

.skiplink {
  position: absolute;
  top: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden; }

.skiplink:focus {
  top: 40px;
  width: auto;
  height: auto;
  background: #000;
  border: 1px solid #000;
  color: #fff;
  font-size: 2rem;
  z-index: 10;
  padding: 10px;
  display: inline-block; }

.menu > ul {
  display: flex; }

.main-nav .wrapper {
  justify-content: space-between; }

.admin-bar header {
  top: 32px; }

.sub-menu {
  display: none;
  position: absolute; }

.menu li:hover .sub-menu, .menu li.active .sub-menu {
  display: block; }

.menu-button {
  display: none;
  width: 45px;
  height: 31px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer; }

.menu-button span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: black;
  border-radius: 2px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out; }

.menu-button span:nth-child(1) {
  top: 0px; }

.menu-button span:nth-child(2) {
  top: 12px; }

.menu-button span:nth-child(3) {
  top: 24px; }

.menu-button.open span:nth-child(1) {
  top: 18px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate(135deg); }

.menu-button.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
  display: none; }

.menu-button.open span:nth-child(3) {
  top: 18px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg); }

@media (max-width: 1200px) {
  .menu {
    display: none; }
  .main-nav .menu > ul {
    flex-direction: column; }
  .menu-button {
    display: block; } }

#comments .navigation {
  padding: 0 0 18px; }

h3#comments-title, h3#reply-title {
  color: #000000;
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 0; }

h3#comments-title {
  padding: 24px 0; }

