#respond dt {
  color: #888888;
  font-size: 12px;
  line-height: 18px; }

#respond dd {
  color: #888888;
  font-size: 12px;
  line-height: 18px; }
  #respond dd code {
    font-size: 11px; }

#respond .form-submit {
